/* General Styles */
body {
  font-family: "Roboto", Arial, sans-serif;
  margin: 0;
  background: #f5f7fa; /* Softer background */
  color: #333;
}

.admin-page {
  max-width: 900px; /* Wider layout */
  margin: 50px auto; /* Centered with vertical spacing */
  padding: 30px;
  background: #ffffff; /* White card-style background */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Subtle shadow */
  border-radius: 8px; /* Rounded corners */
}

.admin-page h1 {
  text-align: center;
  font-size: 32px; /* Larger heading */
  color: #264653; /* Matching accent color */
  margin-bottom: 20px;
}

.toggle-section {
  display: flex;
  justify-content: center;
  margin-bottom: 30px; /* Spacing below buttons */
}

.toggle-section button {
  padding: 10px 20px;
  font-size: 16px;
  font-weight: 600;
  color: #264653;
  background-color: #f0f5f9;
  border: 2px solid #264653;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.toggle-section button.active {
  background-color: #2a9d8f;
  color: white;
  border-color: #2a9d8f;
}

.toggle-section button:hover {
  background-color: #e9c46a;
  color: white;
}

/* Form Section */
.blog-form {
  display: flex;
  flex-direction: column;
  gap: 15px; /* Improved spacing */
  margin-bottom: 40px; /* Spacing between form and content */
}

.blog-form input,
.blog-form textarea {
  padding: 12px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 5px;
  transition: border-color 0.3s ease;
}

.blog-form input:focus,
.blog-form textarea:focus {
  border-color: #2a9d8f;
  outline: none;
  box-shadow: 0 0 5px rgba(42, 157, 143, 0.3);
}

.blog-form textarea {
  resize: vertical;
  min-height: 120px;
}

.blog-form button {
  padding: 12px 20px;
  font-size: 18px;
  color: white;
  background-color: #2a9d8f;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.blog-form button:hover {
  background-color: #21867a;
  transform: translateY(-2px);
}

.blog-form button.cancel {
  background-color: #e76f51;
}

.blog-form button.cancel:hover {
  background-color: #d25a3c;
}

/* Content List */
.blog-list {
  margin-top: 30px;
}

.blog-list h2 {
  font-size: 24px;
  color: #264653;
  margin-bottom: 15px;
}

.blog-item {
  background: #ffffff;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  margin-bottom: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.blog-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.blog-item h3 {
  margin-bottom: 10px;
  font-size: 20px;
  color: #264653;
}

.blog-item p {
  font-size: 16px;
  color: #555;
  margin-bottom: 10px;
  line-height: 1.5;
}

.blog-item a {
  font-size: 16px;
  color: #2a9d8f;
  text-decoration: none;
}

.blog-item a:hover {
  color: #21867a;
}

/* Action Buttons */
.actions {
  display: flex;
  gap: 10px;
}

.actions button {
  padding: 8px 15px;
  font-size: 14px;
  font-weight: 600;
  color: white;
  background-color: #e76f51;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.actions button.edit {
  background-color: #2a9d8f;
}

.actions button:hover {
  transform: translateY(-2px);
}

.actions button.edit:hover {
  background-color: #21867a;
}

.actions button.delete:hover {
  background-color: #d25a3c;
}

/* Login Page */
.login-page {
  max-width: 400px;
  margin: 50px auto;
  padding: 30px;
  background: #ffffff;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.login-page h1 {
  text-align: center;
  font-size: 28px;
  color: #264653;
  margin-bottom: 20px;
}

.login-page form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.login-page input {
  padding: 12px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.login-page input:focus {
  border-color: #2a9d8f;
  outline: none;
  box-shadow: 0 0 5px rgba(42, 157, 143, 0.3);
}

.login-page button {
  padding: 12px 20px;
  font-size: 16px;
  color: white;
  background-color: #2a9d8f;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.login-page button:hover {
  background-color: #21867a;
  transform: translateY(-2px);
}

.login-page .error {
  color: #e76f51;
  font-size: 14px;
  margin-top: 10px;
  text-align: center;
}

.blog-form select {
  padding: 12px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #ffffff;
  color: #333;
  transition: border-color 0.3s ease;
}

.blog-form select:focus {
  border-color: #2a9d8f;
  outline: none;
  box-shadow: 0 0 5px rgba(42, 157, 143, 0.3);
}

.blog-form input,
.blog-form textarea,
.blog-form select {
  padding: 12px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 5px;
  transition: border-color 0.3s ease;
}

.blog-form input:focus,
.blog-form textarea:focus,
.blog-form select:focus {
  border-color: #2a9d8f;
  outline: none;
  box-shadow: 0 0 5px rgba(42, 157, 143, 0.3);
}