/* General Styles */
body {
  margin: 0;
  font-family: "Roboto", Arial, sans-serif;
  background: #f5f7fa; /* Softer background */
  color: #333;
  scroll-behavior: smooth;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Header Styles */
.header {
  position: sticky;
  top: 0;
  z-index: 1000;
  background: linear-gradient(to right, #1f2937, #3b82f6); /* Gradient for header */
  color: white;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 10px 0;
}

.nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 40px;
}

.logo {
  font-size: 28px;
  font-weight: bold;
  color: white;
  text-decoration: none;
  transition: transform 0.3s, color 0.3s;
}

.logo:hover {
  color: #67c7eb;
  transform: scale(1.1);
}

.nav {
  display: flex;
  gap: 20px;
}

.nav-link {
  font-size: 16px;
  color: white;
  text-decoration: none;
  padding: 8px 16px;
  border-radius: 20px;
  transition: background 0.3s, transform 0.3s;
}

.nav-link:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: scale(1.1);
}

.nav-link.active {
  background: white;
  color: #1f2937;
}

/* Hero Section */
.hero-section {
  position: relative;
  height: 60vh; /* Adjust height for hero section */
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(
      to right,
      rgba(31, 41, 55, 0.8),
      rgba(59, 130, 246, 0.8)
    ),
    url("./test.jpg") no-repeat center center / cover;
  box-shadow: inset 0 -5px 10px rgba(0, 0, 0, 0.3); /* Subtle shadow effect */
  text-align: center;
}


.hero-overlay h1 {
  font-size: 48px;
  margin-bottom: 10px;
  font-weight: bold;
  color: #67c7eb;
  text-shadow: 2px 2px 6px rgba(0, 0, 0, 0.5);
}

.hero-overlay p {
  font-size: 20px;
  margin-bottom: 20px;
  line-height: 1.6;
  color: #dbeafe;
}

/* Section Styles */
.section {
  padding: 70px 40px;
  text-align: center;
  background: #fff;
  margin-bottom: 30px;
}

.section h2 {
  font-size: 36px;
  margin-bottom: 20px;
  color: #264653;
}

.section p {
  font-size: 18px;
  color: #555;
  line-height: 1.6;
}

/* Card Grid Styles */
.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 25px;
}

.card {
  background: white;
  border-radius: 10px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  padding: 25px;
  transition: transform 0.2s, box-shadow 0.2s;
}

.card:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
}

.card h3 {
  font-size: 22px;
  margin-bottom: 15px;
  color: #264653;
}

.card p {
  color: #555;
  margin-bottom: 10px;
  line-height: 1.5;
}

.card a {
  color: #2a9d8f;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s;
}

.card a:hover {
  color: #e76f51;
}

/* Footer Styles */
.footer {
  text-align: center;
  padding: 20px 40px;
  background: #264653;
  color: white;
  font-size: 16px;
  margin-top: 30px;
}

/* Contact Section */
.contact-section {
  padding: 70px 40px;
  background: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact-container {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  max-width: 1200px;
  width: 100%;
}

.contact-text {
  flex: 1;
  min-width: 350px;
}

.contact-text h2 {
  font-size: 28px;
  color: #264653;
  margin-bottom: 15px;
}

.contact-text p {
  font-size: 16px;
  color: #555;
  line-height: 1.6;
}

.contact-form {
  flex: 1;
  min-width: 350px;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 15px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 5px;
  transition: border-color 0.3s;
}

.contact-form textarea {
  resize: none;
  height: 120px;
}

.contact-form input:focus,
.contact-form textarea:focus {
  border-color: #2a9d8f;
  outline: none;
}

.contact-form button {
  background-color: #2a9d8f;
  color: white;
  font-size: 18px;
  padding: 12px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.contact-form button:hover {
  background-color: #21867a;
  transform: scale(1.05);
}

/* Filters */
.filter-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  flex-wrap: wrap;
  gap: 20px;
}

.filter-input,
.filter-select {
  padding: 10px 15px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.filter-input {
  width: 250px;
}

.filter-select {
  width: 200px;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .hero-section {
    height: 50vh; /* Adjust for smaller screens */
  }

  .hero-overlay {
    padding: 30px;
  }

  .hero-overlay h1 {
    font-size: 36px;
  }

  .hero-overlay p {
    font-size: 18px;
  }

  .cta-button {
    font-size: 16px;
    padding: 12px 20px;
  }
}
